import React, { useState } from "react";
import { Drawer, List, ListItem, ListItemButton, ListItemIcon, ListItemText, IconButton, Divider } from "@mui/material";
import { Home, Dashboard, Brightness4, Brightness7, Menu } from "@mui/icons-material";
import { useTheme } from "@mui/material/styles";

const Sidebar = ({ toggleDarkMode, darkMode }) => {
  const [open, setOpen] = useState(false);
  const theme = useTheme();

  const toggleDrawer = () => setOpen(!open);

  return (
    <>
      <IconButton
        onClick={toggleDrawer}
        sx={{ position: "absolute", top: 16, left: 16, color: theme.palette.text.primary }}
      >
        <Menu />
      </IconButton>

      <Drawer anchor="left" open={open} onClose={toggleDrawer} PaperProps={{ sx: { width: 250, bgcolor: theme.palette.background.default } }}>
        <List>
          <ListItem disablePadding>
            <ListItemButton href="/" sx={{ "&:hover": { bgcolor: "primary.light" } }}>
              <ListItemIcon><Home sx={{ color: "primary.main" }} /></ListItemIcon>
              <ListItemText primary="Home" />
            </ListItemButton>
          </ListItem>

          <ListItem disablePadding>
            <ListItemButton href="/dashboard" sx={{ "&:hover": { bgcolor: "primary.light" } }}>
              <ListItemIcon><Dashboard sx={{ color: "primary.main" }} /></ListItemIcon>
              <ListItemText primary="Dashboard" />
            </ListItemButton>
          </ListItem>

          <Divider sx={{ my: 1 }} />

          <ListItem disablePadding>
            <ListItemButton onClick={toggleDarkMode} sx={{ "&:hover": { bgcolor: "primary.light" } }}>
              <ListItemIcon>{darkMode ? <Brightness7 sx={{ color: "primary.main" }} /> : <Brightness4 sx={{ color: "primary.main" }} />}</ListItemIcon>
              <ListItemText primary={darkMode ? "Light Mode" : "Dark Mode"} />
            </ListItemButton>
          </ListItem>
        </List>
      </Drawer>
    </>
  );
};

export default Sidebar;
