import React, { useState, useEffect } from "react";
import { Container, Typography } from "@mui/material";
import LiveDataCards from "../components/LiveDataCards";

const API_URL = "http://localhost:8000/data";
const WS_URL = "ws://localhost:8000";

const Dashboard = () => {
  const [data, setData] = useState({
    time: "",
    pm25: 0,
    temp: 0,
    humidity: 0,
    wind: 0,
  });

  useEffect(() => {
    const socket = new WebSocket(WS_URL);
    socket.onopen = () => console.log("‚úÖ WebSocket Connected");

    socket.onmessage = (event) => {
      const newData = JSON.parse(event.data);
      console.log("üîπ New Data Received:", newData); // ‚úÖ Debugging

      setData((prevData) => ({
        ...prevData,
        ...newData, // ‚úÖ Only update changed values
      }));
    };

    socket.onerror = (error) => console.error("üö® WebSocket Error:", error);
    socket.onclose = () => console.warn("‚ö†Ô∏è WebSocket Disconnected");

    return () => socket.close();
  }, []);

  return (
    <Container maxWidth="lg" sx={{ py: 4, pl: { xs: 0, md: 10 } }}>
      <Typography variant="h4" color="primary" gutterBottom>
        Real-Time Air Quality Dashboard
      </Typography>

      <LiveDataCards data={data} />  {/* ‚úÖ Data updates every 2 seconds */}
    </Container>
  );
};

export default Dashboard;
